.control {
  min-height: 79vh;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 2rem;
}

table {
  border-collapse: collapse;
  width: 95vw;
}

th,
td {
  text-align: left;
  padding: 8px;
  border: 1px solid #ddd;
}

th {
  background-color: #f2f2f2;
}
.bottom {
  width: 95vw;
  display: flex;
  flex: 1;
  background-color: #ebdcb2;
}

/* Оптимизация для смартфонов */
@media (max-width: 768px) {
  .controll {
      width: 95vw; /* Увеличиваем ширину, чтобы заполнить экран */
      margin-bottom: 20px;
  }
  .control textarea {
      width: 95vw; /* Подгоняем ширину под экран */
      height: 20vh; /* Делаем текстовую область немного ниже */
      font-size: 1em; /* Уменьшаем шрифт для удобства */
      padding: 8px; /* Уменьшаем внутренние отступы */
  }
  .control p {
      font-size: 1em; /* Уменьшаем размер текста предупреждения */
  }
}
