.control {
  display: flex;
  flex-direction: column;
}

.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
}
.mod {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgb(0, 0, 0, 0.5);
  z-index: 2;
}
.discription {
  width: 80%;
  color: aqua;
}
.discription h6 {
  font-size: 16px;
}

.panel {
  width: 60vw;
  height: min-content;
  background-color: #1e1308;
  color: rgb(35, 16, 244);
  overflow: auto;
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5px;
  z-index: 3;
}
.panel p {
  text-align: justify;
  font-size: 18px;
}

.panel h1 {
  margin: 8px;
  padding: 10px;
  font-size: 40px;
  letter-spacing: 5px;
}
.control h4 {
  color: aliceblue;
}

.panel a {
  margin: 15px;
  margin-bottom: 10px;
  font-size: 12px;
  font: italic;
  color: greenyellow;
  text-decoration: none;
  transition-property: all;
  transition-duration: 300ms;
}
.panel a:hover {
  text-decoration: underline;
  color: cyan;
  cursor: pointer;
  font-size: 13px;
}
.panel a:active {
  color: wheat;
}
.link {
  display: flex;
  margin-bottom: 10px;
  width: 80%;
  justify-content: space-around;
}

/* Оптимизация для смартфонов */
@media (max-width: 768px) {
  .controll {
      width: 95vw; /* Увеличиваем ширину, чтобы заполнить экран */
      margin-bottom: 20px;
  }
  .control textarea {
      width: 95vw; /* Подгоняем ширину под экран */
      height: 20vh; /* Делаем текстовую область немного ниже */
      font-size: 1em; /* Уменьшаем шрифт для удобства */
      padding: 8px; /* Уменьшаем внутренние отступы */
  }
  .control p {
      font-size: 1em; /* Уменьшаем размер текста предупреждения */
  }
}